/*
  1) CONFIG - DEFAULT STATE:
*/
const defaultState = {
  input_str: '',
  input_tld: '',
  input: '', // simple string, user-entered, will be parsed into chunks/tld_chunk
  chunks: [], // array of word DB rows: { key: '', poss: {} }
  tld_chunk: undefined, // word DB row: { key: '', poss: {} }
}

/*
  2) REDUCE STATE:
*/
const ThisReducer = (state = defaultState, action) => {
  const newState = { ...state }

  /*
    3) HANDLE ACTIONS:
  */
  switch (action.type) {
    /*
     * Input (query)
     */
    case 'RX__word_input_str':
      newState.input_str = action.data || defaultState.input_str
      break

    case 'RX__word_input_tld':
      newState.input_tld = action.data || defaultState.input_tld
      break

    case 'RX__word_input':
      newState.input = action.data || defaultState.input
      break

    case 'RX__word_chunks':
      newState.chunks = action.data || defaultState.chunks
      break

    case 'RX__word_tld_chunk':
      newState.tld_chunk = action.data || defaultState.tld_chunk
      break
  }
  /*
    4) RETURN (COPY OF) STATE:
  */
  console.log(
    [action.type, action],
    JSON.parse(JSON.stringify(newState)),
  )
  return newState
}

export default ThisReducer
